let prefixes = [
  "Light ",
  "Dark ",
  "Shining ",
  "Misty's ",
  "Sabrina's ",
  "Erika's ",
  "Brock's ",
  "Blaine's ",
  "Giovanni's ",
  "Rocket's ",
  "Lt. Surge's ",
  "Koga's "
]

export default function rename(text, replacement) {

  for (let i = 0; i < prefixes.length; i++) {
    if (text.indexOf(prefixes[i]) !== -1) {
      let regex = new RegExp(prefixes[i], "g");
      text = text.replace(regex, "");
      break;
    }
  }

  for (let i = 0; i < pokemon.length; i++) {
    if (text.indexOf(pokemon[i]) !== -1) {
      let regex = new RegExp(pokemon[i], "g");
      text = text.replace(regex, replacement)
      break;
    }
  }

  return text;
}